<script>
    import firebase from 'firebase'
    import axios from "axios"
    
    export default {
        data() {
            return {
                qActive: true,
                speedCheck: false,
                datasize: 322000,
                ed: 0, tm: 0, sp: 0,
                st: (new Date()).getTime(),
                q1: null, q2: null, q3: null, q4: null, q5: null, q6: null, q7: null, q8: null, q9: null, q10: null,
                q11: null, q12: null, q13: null, q14: null, q15: null,
                re: null,
                colors: { 2: '#F7BA2A', 4: { value: '#FF9900', excluded: true }, 5: '#ff6347', 7: '#dc143c' },
                iconClasses: ['el-icon-help', 'el-icon-help', 'el-icon-help']
            }
        },
        methods: {
            calc(){
                this.re = (this.q1+this.q2+this.q3+this.q4+this.q5+this.q6+this.q7+this.q8+this.q9+this.q10
                        +this.q11+this.q12+this.q13+this.q14+this.q15)/15
                this.qActive =false
            },
            goToTest(){
                this.$router.push('/about').catch(()=>{});
            },
            logout: function(){
                clearTimeout(this.timeoutTimer)
                this.logouotRequest(this.email)
                firebase.auth().signOut();
                clearTimeout(this.timeoutTimer)
                this.$router.push('/')
            },
            logouotRequest: async function(username){
                const params = new URLSearchParams();
                params.append("name",username);
                await axios.post("http://localhost:8040/logout", params)
                //await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/logout", params)
                alert("ログアウトしました")
            },
        },
        mounted: function(){
            this.ed = (new Date()).getTime()
            this.tm = (this.ed - this.st) / 1000
            this.sp = Math.floor(this.datasize /this. tm)
            if(this.sp > 5000000){
                this.speedCheck = true
            }else{
                alert("インターネット速度がかなり遅いです。\nほかの環境で再度お試しください")
                this.logout()
            }
        }
    }
</script>

